import React from 'react'
import { Card } from './Card';

export const Cards = () => {

    const proyects = [1, 2, 3, 4];

    return (
        <div className="cards">
            <ul className="cards__list">
                {
                    proyects.map((proyect, index) => (
                        <Card key={index} />
                    ))
                }
            </ul>
        </div>
    )
}
