import React from 'react'

export const Card = () => {
    return (
        <div className="card">
            <div className="card__info">
                <img
                    src="https://www.adictosaltrabajo.com/wp-content/uploads/2018/05/el_remozado_javascript.imagen.jpg"
                    alt="javascript"
                    className="card__image"
                />
                <div className="card__content">
                    <h1 className="card__title">Tienda online para casas</h1>
                </div>
            </div>
            <div className="card__view animate__animated animate__fadeIn">
                {/* <h3>Choose</h3> */}
                <a href="/" alt="github"><i className="fab fa-github"></i></a>
                <a href="/" alt="deployment"><i className="fas fa-server"></i></a>
            </div>
        </div>
    )
}
