import React from 'react'

export const Contact = () => {
    return (
        <div className="contact" id="contact_mark">
            <div className="contact__info">
                <div className="contact__header">
                    <h1>Contact me.</h1>
                </div>
                <div className="contact__social_network">
                    <a
                        href="https://github.com/RobertoVr"
                        target="_blank"
                        rel="noreferrer"
                        alt="github">
                        <i className="fab fa-github"></i>
                    </a>
                    <a
                        href="https://www.linkedin.com/in/roberto-valdez-rodriguez-314805176/"
                        target="_blank"
                        rel="noreferrer"
                        alt="in">
                        <i className="fab fa-linkedin"></i>
                    </a>
                    {/*  */}
                </div>
                <div className="contact__email">
                    <h3>email@gmail.com</h3>
                </div>
            </div>
        </div>
    )
}
